import { __decorate } from "tslib";
import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        // 文件上传测试
        this.fileUrl = 'http://img.beijixiong.club:4869/';
        this.imageArr = [];
        this.action = { target: '/fileUpload', data: { token: 'rwwXO0wQq5SBEqP7' } };
        this.isUploading = true;
    }
    upload() {
        this.isUploading = true;
        this.$refs.upload.start();
    }
    pause() {
        this.isUploading = false;
        this.$refs.upload.pause();
    }
    retry() {
        this.$refs.upload.retry();
    }
    filesAdded(files) {
        let hasIgnore = false;
        const maxSize = 1 * 1024 * 1024; // 1M
        for (let k in files) {
            const file = files[k];
            if (file.size > maxSize) {
                file.ignore = true;
                hasIgnore = true;
            }
        }
        hasIgnore && this.$createToast({
            type: 'warn',
            time: 2000,
            txt: '文件需要小于1M'
        }).show();
    }
    fileRemove(evt) {
        if (evt.response && evt.response.md5) {
            let str = this.fileUrl + evt.response.md5;
            var index = this.imageArr.indexOf(str);
            if (index > -1) {
                this.imageArr.splice(index, 1);
            }
        }
    }
    fileSuccess(file) {
        let md5 = file.response.md5;
        let fileUrl = this.fileUrl + md5;
        var index = this.imageArr.indexOf(fileUrl);
        if (index === -1) {
            this.imageArr.push(fileUrl);
        }
    }
    handleImgsClick(index) {
        this.initialIndex = index;
        const params = {
            $props: {
                imgs: this.imageArr,
                initialIndex: 'initialIndex',
                loop: false
            },
            $events: {
                change: (i) => {
                    // 必须更新 initialIndex
                    this.initialIndex = i;
                }
            }
        };
        this.$createImagePreview({ ...params }).show();
    }
    imageArrChange(list) {
        this.$emit('input', list);
    }
    valueChange(value) {
        if (value.length > 0) {
            this.$set(this, 'imageArr', value);
        }
    }
    imageRemove(url, index) {
        this.$store.dispatch('removeImage', { url: url }).then((response) => {
            if (response.success) {
                console.log(url);
                console.log('图片删除成功！');
            }
            this.imageArr.splice(index, 1);
        });
    }
};
